<template>
  <div>
        <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" />
      </div>
      <div slot="center" class="header">
        <span>更新地址</span>
      </div>
    </nav-bar>
    <van-address-edit class="address"
      :area-list="areaList"
      show-set-default
      show-delete
      show-search-result
      :search-result="searchResult"
      :address-info="addressInfo"
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
      @change-detail="onChangeDetail"
      @delete="handleDelete"
    >
    </van-address-edit>
  </div>
</template>

<script>
import areaList from 'assets/js/area'
import NavBar from "components/common/navbar/NavBar";
export default {
  name: "Address",
  data() {
    return {
      areaList,
      addressInfo:{},
      searchResult: [],
      addAddress:{},
      uid:localStorage.getItem('uid'),
      id:0
    };
  },
  components: {
    NavBar
  },
  created() {},
  mounted() {
    console.log(this.addressInfo);
    console.log(this.uid);
    this.id = this.$route.query.id;
    this.fetchAddressData(this.id);
  },
  methods: {
 async handleDelete(){
   let res = await this.$http.delete(this.$api.ADDRESS+'/'+this.id);
   if(res.data.code==200){
      this.$toast('删除成功');
           setTimeout(()=>{
             this.$router.go(-1)
      },500)
   }
 },
 async onSave(data) {
      console.log(data.areaCode);
      this.addressInfo = {
        province:data.province,
        city:data.city,
        area:data.county,
        address:data.addressDetail,
        connect_name:data.name,
        connect_phone:data.tel,
        default:data.isDefault?1:0,
        type:"1",
        uid:localStorage.getItem('uid'),
        areaCode:data.areaCode
      }
     let res = await  this.$http.put(this.$api.ADDRESS+'/'+this.id,this.addressInfo)
         console.log(res);
         console.log(this.addressInfo.id);
         if (res.data.code===200){
           this.$toast('修改成功');
           setTimeout(()=>{
             this.$router.go(-1)
           },500)
         }  
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "阳光广场中心",
            address: "山西创业大厦"
          }
        ];
      } else {
        this.searchResult = [];
      }
    },
   async fetchAddressData(id){
      let res = await this.$http.get(this.$api.ADDRESS+'/'+id);
      console.log(res);
      let data = res.data.data;
      this.addressInfo = {
        province:data.province,
        city:data.city,
        county:data.area,
        addressDetail:data.address,
        name:data.connect_name,
        tel:data.connect_phone,
        isDefault:data.default?true:false,
        type:"1",
        id:data.id,
        areaCode:data.areaCode,
      }
    }
  }
};
</script>
<style lang='scss' scoped>
.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
.address{
  margin-top: 1.9rem;
}
</style>
